@import "base/variables";
@import "base/mixins";
@import "base/typography";
@import "vendor/normalize";
@import "vendor/jquery.scrollbar";
@import "vendor/sumoselect.min";

* {
	box-sizing: border-box;
}

h1, h2, h3, h4, p {
    padding: 0;
    margin: 0;
}

html, body {
    font-family: 'OpenSans';
		font-size: 15px;
		min-height: 100vh;
}


.SumoSelect {
	width: 100%;
}

.SumoSelect > .CaptionCont {
	background: transparent;
	border: 1px solid $color-blue;
	border-radius: 5px;
	color: $color-blue-light;
}

.SumoSelect.open > .CaptionCont, .SumoSelect:focus > .CaptionCont, .SumoSelect:hover > .CaptionCont {
	box-shadow: 0 0 2px $color-blue-light;
  border-color: $color-blue-light;
}

.SumoSelect > .CaptionCont > label > i {
	background: url(../img/arrow.svg) no-repeat center center;
}

.SumoSelect > .optWrapper {
	background: $color-black-back;
	border: 1px solid $color-blue-light;
	color: $color-blue;
}

.SumoSelect > .optWrapper > .options {
	&::-webkit-scrollbar {
			width: 3px;
		}
	&::-webkit-scrollbar-thumb {
		width: 3px;
		border-radius: 3px;
		background: $color-blue-light;
	}
	&::-webkit-scrollbar-track {
	  background: transparent; 
	}
}

.SumoSelect > .optWrapper > .options li.opt {
	border-bottom: 1px solid $color-blue-light;


	&:hover {
		color: $color-blue-light;
		background-color: $color-black-back-light
	}
}

.wrapper {
	width: 100%;
	background: linear-gradient(108.42deg, #25223A 10.74%, #08051B 100%);
	min-height: 100vh;
	overflow: hidden;
	color: $color-white;
}

.container {
	max-width: 1220px;
	margin: 0 auto;
}

.header {
	position: fixed;
	z-index: 100;
	top: 0;
	left: 0;
	right: 0;
	background: #18152A;
	box-shadow: 0px 4px 8px rgba(37, 34, 58, 0.07);

	&-container {
		padding: 30px 0 20px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&-item {
		display: flex;
		align-items: center;
	}

	&-link {
		color: $color-blue;
		margin-right: 30px;
		text-decoration: none;
		outline: none;
		transition: .2s;

		&:hover {
			color: #0092FF;
		}

		&.active {
			color: $color-white;
		}

		&.no-mr {
			margin-right: 0px;
		}
	}

	&-avatar {
		height: 32px;
		width: 32px;
		border-radius: 50%;
		background: #FFFFFF;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		margin-right: 10px;
	}

	&-menu {
		z-index: 10;
		padding: 5px 0px;
		position: absolute;
		background: #F3F6FF;
		border-radius: 3px;
		right: 0;
		display: none;

		&__link {
			text-decoration: none;
			color: $color-black;
			padding: 10px 30px 10px 20px;
			display: block;
			transition: .1s;
			&:hover {
				background: $color-blue-light;
				color: $color-white;
			}
		}

		&-container {
			position: relative;
			display: flex;
			align-items: center;
			cursor: pointer;
		}
	}

	&-arrow {
		position: relative;
	}
}

.balance {
	display: flex;
	align-items: center;
	margin-right: 30px;


	img {
		margin-right: 10px;
	}


	&-text {
		color: $color-blue;
	}
}

.line-grey {
	height: 1px;
	background: #E0E0E0;
	margin: 3px 15px;
}

.main-block {
	max-width: 1220px;
	margin: 0 auto 105px;
	min-height: 550px;
	padding: 55px 0 95px;
	background: $color-black-back;
	box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.07);
	border-radius: 10px;


	&-nav{

		&_mb{
			padding: 0 125px;
			margin-bottom: 20px;
		}
	}

	&-head {
		font-style: normal;
		font-weight: 600;
		line-height: normal;
		font-size: 14px;
		color: $color-white;
		margin-bottom: 20px;

		&_with-p {
			padding: 0 125px;
		}
	}

	&-bar {
		height: 55px;
		display: flex;
		align-items: center;
		background: $color-black-back-light;
		padding: 0 140px 0 60px;


		&_center {
			justify-content: center;
			padding: 0 60px;
		}


		&_sb {
			padding: 0 125px;
			justify-content: space-between;


			.header-link {
				margin-right: 0px;
			}


			.main-block-bar-item  {
				flex: 0 1 130px;
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}
	}

	&-content {


		&_p {
			padding: 0 40px;
		}
	}
}

.campaings {
	padding-left: 125px;
	padding-right: 59px;


	&-inn {
		margin-top: 35px;
		overflow: auto;
		height: 310px;
		position: relative;


		&-content {
			margin-top: 0px;
			padding-right: 66px;
		}


		&-row{
			height: 140px;
			display: flex;
			align-items: center;
		}
	}

	&-btn-add {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 0 auto;

		svg {
			margin-bottom: 15px;
		}
	}

	&-item {
		background: $color-black-back-light;
		border-radius: 5px;
		padding: 30px 70px;
		margin-bottom: 30px;
		transition: .2s;

		&:hover {
			background: #0092FF;
		}

		&:last-child {
			margin-bottom: 0px;
		}

		&-img {
			margin-right: 45px;
			height: 80px;

			img {
				height: 100%;
			}
		}

		&-info {
			color: $color-white;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
		}

		&-btns {
			margin-left: auto;
			display: flex;
			flex-direction: column; 
			justify-content: space-around;
			height: 100%;
		}

		&-btn {
			color: $color-white;
			text-decoration: none;
			outline: none;				
		}
	}
}

.block {
	padding: 178px 110px 50px;
}

.pdf {
	display: flex;
	align-items: center;
	text-decoration: none;
	margin-left: auto;

	svg {
		margin-right: 15px;

		path {
			transition: .2s;
		}
	}

	&:hover {
		svg {
			path {
				fill: $color-blue-light;
			}
		}
	}
}

.date-pick {
	display: flex;
	align-items: center;


	&-period {
		margin-right: 50px;
		display: flex;


		&__link {
			display: flex;
			align-items: center;
			margin-left: 10px;
			text-decoration: none;
			color: $color-blue-light;
			flex-wrap: nowrap;
			border-bottom: 1px solid $color-blue-light;

			svg {
				margin-left: 5px;
			}


			&_inline {
				display: inline-block;
				margin-left: 0px;
			}
		}
	}
}

.table {
	width: 100%;
	padding: 0 40px;
	border-collapse: collapse; 

	th {
		font-weight: normal;
		color: $color-blue;
		padding: 15px 12px;
	}


	td {
		text-align: center;
	}

	tbody {


		tr {
			border-bottom: 1px solid #316193;
		}

		td {
			padding: 10px;
		}
	}
}


.main-block-tabs {


	&-item {
		display: none;


		&.active {
			display: block;
		}
	}

	&-item_flex {
		padding-top: 30px;
		&.active {
			display: flex;
			align-items: stretch;
			justify-content: space-between;
			flex: 1;
			.block-form-column {
				justify-content: flex-start;
				.form-input {
					align-items: flex-start;
					margin-bottom: 35px;


					&:last-child {
						margin-bottom: 0px;
					}
				}
			}
		}
	}
}


.main-block-content_payment {
	display: flex;
	align-items: center;
	flex-direction: column;


	.input-text {
		position: relative;
		margin: 100px 0 70px;
		border-bottom: 1px solid $color-blue;
		width: 300px;


		&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				height: 1px;
				background: $color-blue-light;
				opacity: 0;
				width: 0%;
				display: block;
				transition: .2s;
			}
		&:before {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				height: 1px;
				background: $color-blue;
				width: 100%;
				display: block;
				transition: .2s;
			}

		input {
			background: transparent;
			border: none;
			outline: none;
			padding: 10px;
			width: 100%;
			color: $color-blue-light;
		}


		.placeholder {
			position: absolute;
			top: 50%;
			left: 10px;
			transform: translateY(-50%);
			color: $color-blue;
			transition: .2s;
		}

		&.active {
			
			&:after {
				opacity: 1;
				width: 100%;
			}


			.placeholder {
				top: -5px;
				left: 0;
				font-size: 12px;
				color: $color-blue-light;
			}

		}
	}
}

.payment-methods {
	display: flex;
	align-items: center;


	&-item {
		margin-right: 50px;


		&:last-child {
			margin-right: 0px;
		}
	}


	&-head {
		text-align: center;
		margin-bottom: 40px;
	}
}

.payment-history {
	padding: 25px 0;
	display: flex;
	justify-content: space-between;
	align-items: center;


	&-wrapper {
		padding: 15px 125px;
	}


	&-item {
		flex: 0 1 130px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.scrollbar-rail > .scroll-element {
	background: transparent;
}

.scrollbar-rail > .scroll-element.scroll-y .scroll-bar {
	  left: 4px;
    min-height: 10px;
    width: 3px;
}

.scrollbar-rail > .scroll-element .scroll-bar {
	background: #0092FF;
}

.scrollbar-rail > .scroll-element .scroll-element_outer:hover .scroll-element_size {
	background: #0092FF;
}


.registration-head {
	padding: 0 345px;
	margin-bottom: 55px;
}

.registration {
	display: none;
	width: 1220px;
	&.active {
		display: block;
		position: fixed;
		z-index: 200;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
	}
	

	&-main {
		display: flex;
		align-items: stretch;	
		padding: 0 25px;
	}
}

.block-photo-upload {
	width: 295px;
	flex: 0 1 295px;
	border-right: 5px;
	background: $color-black-back-light;
	padding: 40px;
	margin-right: 30px;
	display: flex;
	flex-direction: column;
	align-items: center;


	&__avatar {
		display: inline-block;
		margin-top: 45px;
		width: 150px;
		height: 150px;
		border-radius: 50%;
		overflow: hidden;
		background: url(../img/avatar.png);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: contain;
		margin-bottom: 40px;
	}


	&__link {
		display: flex;
		align-items: center;
		white-space: nowrap;
		font-size: 15px;
		color: $color-blue-light;
		text-decoration: underline;
		margin-bottom: 20px;
		cursor: pointer;

		svg {
			margin-right: 10px;
		}
	}

	&__input {
		display: none;
	}

	&__description {
		font-size: 14px;
		color: $color-blue;
	}
}

.block-form {
	padding: 50px 45px;
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	background: $color-black-back-light;
	border-radius: 5px;
	flex: 1;


	&_column {
		flex-direction: column;
	}


	&-column {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex: 1
	}
}

.form-input {
	display: flex;
	align-items: center;
	

	input {
		width: 200px;
		height: 28px;
		background: transparent;
		border: 1px solid $color-blue;
		border-radius: 3px;
		color: $color-blue-light;
		outline: none;
		padding: 0 5px;
		transition: .3s;
	}


	textarea {
		width: 200px;
		height: 56px;
		background: transparent;
		border: 1px solid $color-blue;
		border-radius: 3px;
		color: $color-blue-light;
		outline: none;
		padding: 5px;
		transition: .3s;
		resize: none;
		&::-webkit-scrollbar {
			width: 3px;
		}
		&::-webkit-scrollbar-thumb {
			width: 3px;
			border-radius: 3px;
			background: $color-blue-light;
		}
		&::-webkit-scrollbar-track {
		  background: transparent; 
		}
	}


	&__descr {
		text-align: right;
		color: $color-blue;
		padding: 0 10px 0 0px;
		flex: 0 1 50%;
		transition: .3s;
	}


	&.active {
		.form-input__descr {
			color: $color-blue-light;
		}
		input {
			border: 1px solid $color-blue-light;
		}
		textarea {
			border: 1px solid $color-blue-light;
		}
	}


	&_width {


		textarea {
			width: 270px;
		}


		input {
			width: 270px;
		}
	}


	&_ai-center {
		align-items: center !important;
	}
}


.block-btns {
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 1220px;
	margin: 0 auto;
	padding: 40px 0;


	&_ml {
		padding: 0;
		padding-left: 100px;

	}
}

.btn-cancel {
	width: 294px;
	height: 42px;
	border: 1px solid $color-blue;
	box-sizing: border-box;
	border-radius: 50px;
	color: #FFFFFF;
	background: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
	outline: none;
	transition: .3s;

	&:hover {
		border: 1px solid $color-blue-light;
		background: $color-blue-light;
	}
}

.btn-submit {
	width: 294px;
	height: 42px;
	border: 1px solid $color-blue;
	box-sizing: border-box;
	border-radius: 50px;
	color: #FFFFFF;
	background: $color-blue;
	outline: none;
	transition: .3s;
	cursor: pointer;


	&:hover {
		border: 1px solid $color-blue-light;
		background: $color-blue-light;
	}
}

.mr-30 {
	margin-right: 30px;
}


.address-list {
	height: 190px;
	overflow: hidden;
	padding: 0 20px 0 0 !important;

	&-item {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		margin-bottom: 25px;


		&:last-child {
			margin-bottom: 0px;
		}


		&__btn {
			margin-right: 0px;
			margin-left: 20px;
		}

		&__name {
			flex: 0 1 250px;
		}
	}
}


.address-add {

	
	&__head {
		font-size: 15px;
		color: $color-blue;
		padding-left: 130px;
		margin-bottom: 25px;
	}


	&__inputs {
		padding-left: 130px;
		margin-bottom: 15px;

		.form-input {
			margin-bottom: 15px;
			align-items: center !important;
			
			&__descr {
				flex: 0 1;
			}


			input {
				width: 235px;
			}
		}
	}

	&__btn {
		padding-left: 130px;
		display: flex;
		justify-content: flex-end;

		.btn-submit {
			width: 235px;
		}
	}
}

#edit {
	margin-bottom: 0px;
}

#faq {

}

.main-block-content_card {
	margin: 25px 25px 0 25px;
	background: $color-black-back-light;
	border-radius: 5px;
	padding: 30px 100px 50px;
}

.faq-main-body {


	.form-input {
		align-items: flex-start;
		margin-bottom: 30px;

		.form-input__descr {
			flex: 0 1 190px;
			margin-right: 50px;
			text-align: left;
		}
	}
}

.form-input__radio {
	display: flex;
	align-items: center;
	cursor: pointer;
	margin-bottom: 20px;

	.radio-circle {
		width: 16px;
		height: 16px;
		position: relative;
		border-radius: 50%;
		border: 1px solid $color-blue;
		margin-right: 20px;

		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%) scale(0);
			height: 8px;
			width: 8px;
			border-radius: 50%;
			background: $color-blue;
			transition: .3s;
		}
	}
	

	input[type="radio"] {
		display: none;
		
		&:checked ~ .radio-circle {
			border: 1px solid $color-blue-light;
		}

		&:checked ~ .radio-circle:after {
			background: $color-blue-light;
			transform: translate(-50%, -50%) scale(1);
		}
	}
}


.faq-main {
	padding: 50px 0 25px;
}

.form-input-radio-wrapper {
	margin-bottom: 10px;
}


.form-input__upload {
	display: block;
	margin-left: 240px;
	cursor: pointer;
	

	input[type="file"] {
		display: none;
	}
	
	
	&_link {
		color: $color-blue-light;
		text-decoration: underline;
		margin-bottom: 10px;
	}


	&_descr {
		color: $color-blue;
		text-decoration: none;
	}
}


.form-input-time {
	width: 80px;
	height: 28px;
	color: $color-blue-light;
	background: transparent;
	border-radius: 5px;
	border: 1px solid $color-blue;


	&-wrapper {
		margin: 0 20px 0 10px;
	}
}

.faq {
	&-content {
		padding: 90px 0;
		width: 60%;
	}
	

	&-theme {
		margin-bottom: 30px;

		&:last-child {
			margin-bottom: 0px;
		}


		&__text {
			font-weight: 600;
		}


		&-main {
			display: none;
			padding: 30px 0 25px 30px;
		}


		&__arrow {
			display: flex;
			align-items: center;
			justify-content: center;
			transition: .3s;
		}


		&-top {
			display: flex;
			justify-content: space-between;
			align-items: center;
			cursor: pointer;


			&.active {
				.faq-theme__arrow {
					transform: rotate(180deg);				
				}
			}
		}
	}


	&-question {
		margin-bottom: 35px;

		&-main {
			display: none;
		}


		&-top {
			display: flex;
			align-items: center;
			justify-content: space-between;
			cursor: pointer;
			margin-bottom: 10px;


			&:hover {
				.faq-question__text {
					color: $color-blue-light;
				}

				.faq-question__arrow {
					path {
						fill: $color-blue-light;
					}
				}
			}

			&.active {
				.faq-question__arrow {
					transform: rotate(180deg);
				}

				.faq-question__text {
					color: $color-blue-light;
				}

				.faq-question__arrow {
					path {
						fill: $color-blue-light;
					}
				}
			}
		}

		&__text {
			color: $color-blue;
			transition: .3s;
		}


		&__arrow {
			display: flex;
			align-items: center;
			justify-content: center;
			transition: .3s;
			path {
				transition : .3s;
			}
		}
	}
}